import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            title: '合作机构',   
			search:'',
			haveMore:true,
			list:[],
			inputFlag:false,
			inspctArea: 0,
			area:null,
			user: this.$store.state.userInfo,
			showStae:'circle',
			chooseData:null,
			save:JSON.parse(localStorage.getItem('saveName')),
			contrastCensorateId:null,
			listShow:true,
			checkIdshow:1
        }
    },
    created(){
        this.pull()
		//console.log(this.$store.state.inspctArea)
		//localStorage.removeItem('saveName')
    },
    mounted(){
       // this.getMore()
	   if(this.save){
			this.showStae = "success-circle"
			if(!this.$route.query.id){
				this.$router.push(`/inspect/pharmacy?id=1`)
			}
	   }
	   if(!this.$route.query.id){
			this.checkIdshow = 2
	   }else{
		   this.checkIdshow = 1
	   }
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        searchFor(search){
            this.pull()
        },
        pull(){
			if(this.$store.state.inspctArea){
				this.inspctArea = this.$store.state.inspctArea.AreaId
				this.area = this.$store.state.inspctArea.AreaName
				this.$post('InspectionAPI/Censorate/QuerySecondCensoratePage',{
				    CensorateName: this.search,
					CensorateCode:'',
					AreaId: this.inspctArea,
					AreaName:"",
					PageIndex:1,
					PageSize:100,
				}).then(o => {
					const list = o.ReturnData
					if(o.ReturnData && o.ReturnData.length){
						this.listShow = true
					}else{
						this.listShow = false
					}
					this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ 
					   DrId: this.user.Data.User.DoctorId, //823
					}).then(o => {
						list.forEach(a => {
							if(a.CensorateId === o.CensorateId){
								a.state = 'success'
								this.chooseData = a
							}else{
								a.state = 'circle' 
							}
						})
						this.list = list
						this.contrastCensorateId = o.CensorateId		
					})
				})
			}else{
				this.$bridge.callhandler('pullArea','',(data) => {
					if(data){
						this.$post('InspectionAPI/Censorate/QuerySecondCensoratePage',{
						    CensorateName: this.search,
							CensorateCode:'',
							AreaId: '',
							AreaName:data,
							PageIndex:1,
							PageSize:100,
						}).then(o => {
							this.area = data
							const list = o.ReturnData
							if(o.ReturnData && o.ReturnData.length){
								this.listShow = true
							}else{
								this.listShow = false
							}
							this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ 
							   DrId: this.user.Data.User.DoctorId,
							}).then(o => {
								list.forEach(a => {
									if(a.CensorateId === o.CensorateId){
										a.state = 'success'
										this.chooseData = a
									}else{
										a.state = 'circle' 
									}
								})
								this.list = list
								this.contrastCensorateId = o.CensorateId
							})
						})
					}else{
						this.$post('InspectionAPI/Censorate/QuerySecondCensoratePage',{
						    CensorateName: this.search,
							CensorateCode:'',
							AreaId: 0,
							AreaName:'',
							PageIndex:1,
							PageSize:100,
						}).then(o => {
							const list = o.ReturnData
							if(o.ReturnData && o.ReturnData.length){
								this.listShow = true
							}else{
								this.listShow = false
							}
							this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ 
							   DrId: this.user.Data.User.DoctorId,
							}).then(o => {
								list.forEach(a => {
									if(a.CensorateId === o.CensorateId){
										a.state = 'success'
										this.chooseData = a
									}else{
										a.state = 'circle' 
									}
								})
								this.list = list
								this.contrastCensorateId = o.CensorateId
							})
						})
					}
				    responseCallback('接收到方法')
				})
			}
        },
		organizationChoose(i){
			if(i.state == "circle"){
				this.list.forEach(a => {
					a.state = 'circle' 
				})
				i.state = 'success'
			}
			this.chooseData = i
		},
        circleSate(){
			if(this.showStae == "circle"){
				this.showStae = "success-circle"
			}else{
				this.showStae = "circle"
			}
		},
		btnSave(){
			var c = 0;
			this.list.forEach(a => {
				if(a.state.indexOf("success") != -1){
					c = 1
				}
			})
			if(c==0){
				return this.$toast('请选择合作机构')
			}
			if(this.showStae == "success-circle"){
				if(this.save){
					localStorage.setItem('saveName', '')
					localStorage.setItem('saveName', JSON.stringify(this.save))
				}else{
					localStorage.setItem('saveName', JSON.stringify(1))
				}
			}
			if(this.contrastCensorateId == this.chooseData.CensorateId){
				if(this.$route.query.id){
					this.$router.back()
				}else{
					this.$router.push(`/inspect/pharmacy`)
				}
			}else{
				this.$post('InspectionAPI/Censorate/SaveDrCensorate',{ //保存医生合作检查机构
					DrId:this.user.Data.User.DoctorId,
					DrName:this.user.Data.User.DoctorName,
					CensorateId:this.chooseData.CensorateId,
					CensorateName:this.chooseData.CensorateName
				 }).then(o => {
					this.$store.dispatch('pushcheckCkooseList',[])
					this.$store.dispatch('pushisShowXC',false)
					this.$store.dispatch('pushsaveInput',null)
					if(this.$route.query.id){
						this.$router.back()
					}else{
						this.$router.push(`/inspect/pharmacy`)
					}
				 })
			}
			
		},
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        }
    }
}



